import React from "react";

import { commonArgs } from "../../configs/types";
import data, { contact_type } from "../../configs/about/data";
import { langs } from "../../configs/site";
interface ContactWayProps {
  way: contact_type;
}

const ContactWay: React.FC<ContactWayProps> = ({ way }) => (
  <a href={way.url} className="w-full group block hover:bg-[color]">
    <div className="flex items-center justify-center px-8 py-4 rounded-lg shadow-md hover:shadow-xl">
      <div className="flex items-center  px-8 py-4">
        <way.icon className="w-6 h-6 mr-3 text-[color]-400" />
        <div className="font-medium text-[color] truncate">{way.title}</div>
      </div>
      <div className="ml-auto text-sm text-gray-500">{way.content}</div>
    </div>
  </a>
);

const About: React.FC<commonArgs> = ({ lang }) => {
  const keylang = lang as keyof typeof langs;
  const { title, content, contact_ways } = data[keylang];

  return (
    <section className="container mx-auto px-4 pt-10 pb-8">
      <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
        <div className="w-full">
          <img
            src={data.image}
            alt={title}
            className="w-full rounded-lg shadow-md"
          />
        </div>
        <div className="max-w-md col-span-1">
          <h2 className="text-2xl font-semibold mb-4">{title}</h2>
          <div className="text-gray-700 prose mb-6">{content}</div>
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
            {contact_ways.ways.map((way, index) => (
              <ContactWay key={index} way={way} />
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
